import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import style from "./style/index.scss";
import fontawesome from "./assets/fontawesome/css/all.css";

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  style,
  fontawesome,
  render: h => h(App)
}).$mount("#app");
