import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    articlesTest: {
      sectionOne: [
        { title: "All status effects and immunity Traits", route: "page1" },
        { title: "Enhancing Troops with Medals", route: "page2" },
        { title: "Medals, Badges and Tokens", route: "page9" }
      ],
      sectionTwo: [
        { title: "Kingdom Power", route: "page4" },
        { title: "Kingdoms Overview", route: "page5" },
        { title: "Explore Mode", route: "page8" }
      ],
      sectionThree: [{ title: "Factions", route: "page3" }],
      sectionFour: [
        { title: "Guild overview", route: "page6" },
        { title: "Tower of Doom", route: "page7" }
      ]
    }
  },
  getters: {
    sectionOne: state => {
      return state.articlesTest.sectionOne;
    },
    sectionTwo: state => {
      return state.articlesTest.sectionTwo;
    },
    sectionThree: state => {
      return state.articlesTest.sectionThree;
    },
    sectionFour: state => {
      return state.articlesTest.sectionFour;
    },
    filterSectionOne: state => search => {
      return state.articlesTest.sectionOne.filter(article =>
        article.title.toLowerCase().includes(search.toLowerCase())
      );
    },
    filterSectionTwo: state => search => {
      return state.articlesTest.sectionTwo.filter(article =>
        article.title.toLowerCase().includes(search.toLowerCase())
      );
    },
    filterSectionThree: state => search => {
      return state.articlesTest.sectionThree.filter(article =>
        article.title.toLowerCase().includes(search.toLowerCase())
      );
    },
    filterSectionFour: state => search => {
      return state.articlesTest.sectionFour.filter(article =>
        article.title.toLowerCase().includes(search.toLowerCase())
      );
    }
  },
  mutations: {},
  actions: {},
  modules: {}
});
