<template>
  <div class="search">
    <label>
      <input
        type="text"
        v-model.trim="search"
        placeholder="Search article..."
        @keydown.enter="focusSearch"
        @mousedown="focusSearch"
      />
      <span class="search-icon">
        <i class="fas fa-search"></i>
      </span>
      <span
        class="search-close"
        @click="
          closeSearch();
          cleanSearch();
        "
      >
        <i class="fas fa-times"></i>
      </span>
    </label>
    <transition name="fade">
      <div class="search-results" v-show="results">
        <p v-if="filteredList.length == 0">{{ notFound }}</p>
        <p v-for="article in filteredList" :key="article.id" class="hr">
          <router-link :to="{ name: article.route }">{{
            article.title
          }}</router-link>
        </p>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "search",
  data() {
    return {
      search: "",
      results: false,
      notFound: "No results...",
      filtered: []
    };
  },
  methods: {
    focusSearch() {
      this.results = true;
    },
    closeSearch() {
      this.results = false;
    },
    cleanSearch() {
      this.search = "";
    },
    closeOut() {
      if (event.target.tagName !== "INPUT" && this.results == true) {
        this.closeSearch();
      }
    }
  },
  computed: {
    articles() {
      return this.$store.state.articles;
    },
    filteredList() {
      const filterSectionOne = this.$store.getters.filterSectionOne(
        this.search
      );
      const filterSectionTwo = this.$store.getters.filterSectionTwo(
        this.search
      );
      const filterSectionThree = this.$store.getters.filterSectionThree(
        this.search
      );
      const filterSectionFour = this.$store.getters.filterSectionFour(
        this.search
      );

      return this.search.length < 3
        ? this.filtered
        : filterSectionOne.concat(
            filterSectionTwo,
            filterSectionThree,
            filterSectionFour
          );
    }
  }
};
</script>
